<template>
  <div class="tabs-bar">
    <div class="tab-nav">
      <div class="tab-left" @click="onNavLeft"><</div>
      <div class="tab-right" @click="onNavRight">></div>
    </div>
    <div class="tab" :class="{'tab-active': i == activeTab}" v-for="(file, i) in currentFiles">
      <span class="tab-name" @click="onClickTab(i)">{{file.getFilename()}}</span>
      <span class="tab-close" @click="onRemoveTab(i)">&times;</span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      activeTab: 0
    }
  },
  methods: {
    onClickTab(i) {
      this.activeTab = i
      let files = this.$store.state.filesystem.currentFiles
      this.$store.dispatch('setCurrentFileId', files[i].getId())
    },
    onRemoveTab(i) {
      // this.files.splice(i, 1)
      let files = this.$store.state.filesystem.currentFiles
      this.$store.dispatch('closeFile', files[i].getId())
    },
    onNavLeft() {
      if (this.activeTab > 0) {
        this.activeTab -= 1
      let files = this.$store.state.filesystem.currentFiles
      this.$store.dispatch('setCurrentFileId', files[this.activeTab].getId())
      }
    },
    onNavRight() {
      if (this.activeTab < this.files.length-1) {
        this.activeTab += 1
      let files = this.$store.state.filesystem.currentFiles
      this.$store.dispatch('setCurrentFileId', files[this.activeTab].getId())
      }
    }
  },
  computed: {
    currentFiles: function () {
      return this.$store.state.filesystem.currentFiles
    },
    currentFileId: function () {
      return this.$store.state.filesystem.currentFileId
    }
  },
  watch: {
    currentFileId (id) {
      // change activetab when id changes
      let ids = this.currentFiles.map(f => f.getId())
      this.activeTab = ids.indexOf(id)
    }
  }
}
</script>

<style>
.tabs-bar {
  display: flex;
  background: #f5f5f5;
}
.tab {
  width: 100px;
  padding: 5px 10px;
  font-size: 13px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.tab-name {
  user-select: none;
}
.tab-close {
  opacity: 0;
  user-select: none;
  font-size: 15px;
}
.tab:hover {
  opacity: 0.6;
}
.tab:hover .tab-close {
  opacity: 1;
}
.tab-active {
  background: #fafafa;
}
.tab-active .tab-close {
  opacity: 1;
}
.tab-nav {
  font-size: 13px;
  display: flex;
}
.tab-left {
  cursor: pointer;
  padding: 5px;
  user-select: none;
}
.tab-right {
  cursor: pointer;
  padding: 5px;
  user-select: none;
}
.tab-left:hover, .tab-right:hover {
  opacity: 0.6;
}
</style>



