<template>
  <ul class="files">
    <li class="folders-item" v-bind:style="{paddingLeft: paddingLeft}" @click="onClickFile">
      <img :src="iconImg" class="file-img" v-if="!editMode">
      <DeleteFolder class="icon" v-if="editMode" viewBox="0 0 25 25" height="50" width="50" @click.native="onRemoveFile"/>
      <div class="folders-item-name">{{file.getFilename()}}</div>
    </li>
    <FolderItem v-for="f in subfiles" :file="f" v-if="showSubfiles && subfiles.length > 0 && f.getLocation() == file.getFullLocation()" :editMode="editMode"/>
  </ul>
</template> 

<script>
import Folder from '../models/folder'
import folderImg from '../assets/sidebar/folder.png'
import folderExpandedImg from '../assets/sidebar/folder_expanded.png'
import fileImg from '../assets/icons/file_type_default.png'
import DeleteFolder from '../assets/svg/delete-folder.svg'

export default {
  name: 'FolderItem',
  props: ['file', 'editMode'],
  components : {
    'DeleteFolder': DeleteFolder
  },
  data () {
    return {
      activeFile: false,
      showSubfiles: false
    }
  },
  methods: {
    onClickFile () {
        if (this.file instanceof Folder) {
          this.showSubfiles = !this.showSubfiles
        } else if (!this.editMode) {
          // open file
          this.$store.dispatch('readFile', this.file.getId())
        }
      
    },
    onRemoveFile () {
      this.$store.dispatch('removeFile', this.file.getId())
    },
    getFile (id) {
      return this.$store.getters.getSubfile(id)
    },
    isFolder () {
      return this.file instanceof Folder
    }
  },
  computed: {
    subfiles: function () {
      return this.isFolder() ? this.file.getFiles().map(id => this.getFile(id)) : []
    },
    paddingLeft: function () {
      let levels = this.file.getLocation().split('/').filter(i => i != "").length+1
      return `${levels*15}px`
    },
    iconImg: function () {
      let folder = this.showSubfiles ? folderExpandedImg : folderImg
      return this.isFolder() ? folder : fileImg
    }
  }
}
</script>

<style>
.file-img {
  height: 15px;
  transform: translateY(2px);
}
.folders-item {
  padding: 5px 15px;
  font-size: 12px;
  cursor: pointer;
  user-select: none;
  display: flex;
}
.folders-item:hover {
  background: rgba(17, 17, 17, 0.05);
}
.folders-item-active {
  background: rgba(17, 17, 17, 0.05);
}
.folders-item-name {
  margin-left: 2px;
}
.subfiles {
  padding-left: 15px;
}
.files .folders-item {
  padding-left: 15px;
}
</style>



