<template>
  <div class="folders-container">
    <h5 class="folders-title">
      <span class="folders-title-name">
        Folders
      </span>
      <span class="folders-title-btns" v-if="!editMode">
        <AddFile 
          @click.native="onAddFile"
          class="icon" viewBox="0 0 25 25" 
          height="50" width="50"/>
        <AddFolder 
          @click.native="onAddFolder"
          class="icon" viewBox="0 0 25 25" height="50" width="50"/>
      </span>
      <span class="folders-edit-dismiss" v-if="editMode" @click="onClickEdit">
        Done
      </span>
    </h5>
    <div class="folders-placeholder" v-if="files.length == 0">
      No files yet
    </div>
    <ul class="folders">
        <FolderItem :file="file" v-for="file in files" v-if="file && file.getLocation() == '/' " :editMode="editMode"/>
    </ul>
    <div class="settings-btn">
      <EditFolder
        @click.native="onClickEdit"
          class="icon" viewBox="0 0 25 25" height="50" width="50"/>
    </div>
  </div>
</template>

<script>
import Folder from '../models/folder'
import AddFile from '../assets/svg/add-file.svg'
import AddFolder from '../assets/svg/add-folder.svg'
import EditFolder from '../assets/svg/edit-folder.svg'
import FolderItem from './Folder'
export default {
  props: ['toggleModal'],
  components: {
    'AddFile': AddFile,
    'AddFolder': AddFolder,
    'FolderItem': FolderItem,
    'EditFolder': EditFolder
  },
  data () {
    return {
      editMode: false
    }
  },
  methods: {
    onAddFile () {
      this.toggleModal('file')
    },
    onAddFolder () {
      this.toggleModal('folder')
    },
    checkClass (obj) {
      return obj instanceof Folder
    },
    onClickEdit () {
      this.editMode = !this.editMode
    }
  },
  computed: {
    files: function () {
      return this.$store.state.filesystem.files
    }
  }
}
</script>

<style>
  .folders-container {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  .folders {
    flex: 1;
  }
  .folders-title {
    margin: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  .icon {
    height: 16px;
    width: 16px;
    cursor: pointer;
    fill: rgba(17,17,17,0.4);
  }
  .icon:hover {
    fill: #111;
  }
  .folders-title-btns {
    white-space: nowrap;
  }
  .folders-edit-dismiss {
    font-size: 11px;
    color: #777;
    cursor: pointer;
  }
  .folders-edit-dismiss:hover {
    opacity: 0.6;
  }
  .settings-btn {
    display: flex;
    justify-content: flex-end;
    padding: 15px;
  }
  .folders-placeholder {
    margin: 50px auto;
    font-size: 12px;
    color: #999;
  }
</style>



