import Vue from 'vue'
import Vuex from 'vuex'
// modules
import filesystem from './modules/filesystem'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    filesystem
  },
  strict: debug
})


