<template>
  <div class="editor-container" id="editorWindow">
    <TopBar :onClickFullscreen="onClickFullscreen" :fullscreen="fullscreen"/>
    <Editor :disable="showModal" :toggleModal="toggleModal"/>
    <FormModal v-if="showModal" :toggleModal="toggleModal" :mode="mode"/>
  </div>
</template>

<script>
import TopBar from '../components/TopBar'
import Editor from '../components/Editor'
import FormModal from '../components/FormModal'
export default {
  components: {
    'TopBar': TopBar,
    'Editor': Editor,
    'FormModal': FormModal
  },
  data () {
    return {
      showModal: false,
      mode: 'file',
      fullscreen: false,
      resize: false
    }
  },
  methods: {
    toggleModal (mode) {
      this.showModal = !this.showModal
      this.mode = mode ? mode : 'file'
    },
    onClickFullscreen () {
      let el = document.getElementById('editorWindow')
      if (this.fullscreen) {
        this.launchIntoFullscreen(el)
      } else {
        this.exitFullscreen(el)
      }
      this.fullscreen = !this.fullscreen
    },
    launchIntoFullscreen (element) {
      if(element.requestFullscreen) {
        element.requestFullscreen();
      } else if(element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if(element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if(element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },
    exitFullscreen (element) {
        if (element.exitFullscreen) {
            element.exitFullscreen();
        } else if (element.webkitExitFullscreen) {
            element.webkitExitFullscreen();
        } else if (element.mozCancelFullScreen) {
            element.mozCancelFullScreen();
        } else if (element.msExitFullscreen) {
            element.msExitFullscreen();
        }
    }
  }
}
</script>

<style>
.editor-container {
  display: flex;
  flex-direction: column;
  background-color: #fafafa;
  height: 100%;
  width: 100%;
}
</style>



