class File {
  constructor(file) {
    if (typeof file == 'string') {
      let path = file.split('/')
      // location of file not including the file
      let location = path.slice(0, path.length-1).join('/')
      let filename = path[path.length-1]
      let ext = filename.split('.')[1]
      this._id = Math.random().toString(36).substring(2)
      this._name = filename.split('.')[0]
      this._location = location[0] != '/' ? '/' + location : location
      this._extension = ext ? ext : ''
      this._content = ['']
    } else {
      let {_id, _name, _location, _extension, _content} = file
      this._id = _id
      this._name = _name
      this._location = _location
      this._extension = _extension
      this._content = _content
    }
  }

  getId() {
    return this._id
  }

  getName() {
    return this._name
  }

  getFilename() {
    return this._extension != '' ? this._name +'.'+ this._extension : this._name
  }

  getLocation() {
    return this._location
  }

  getContent() {
    return this._content
  }

  setName(name) {
    this._name = name
  }

  saveContent(content) {
    this._content = content
  }

}

export default File


