class Folder {
  constructor(file) {
    if (typeof file == 'string') {
      let path = file.split('/')
      let location = path.slice(0, path.length-1).join('/')
      let name = path[path.length-1]
      this._id = Math.random().toString(36).substring(2)
      this._name = name
      this._location = location[0] != '/' ? '/' + location : location
      this._files = []
    } else {
      let {_id, _name, _location, _files} = file
      this._id = _id
      this._name = _name
      this._location = _location
      this._files = _files
    }
  }

  getId() {
    return this._id
  }

  getFilename() {
    return this._name
  }

  getLocation() {
    return this._location
  }

  getFiles() {
    return this._files
  }

  getFullLocation() {
    if (this._location != '/') {
      return this._location +'/'+ this._name
    } else {
      return this._location + this._name
    }
  }
  
  setName(name) {
    this._name = name
  }
  setFiles (file_ids) {
    this._files = file_ids
  }

  addFile(file_id) {
    this._files.push(file_id)
  }

  removeFile(file_id) {
    this._files = this._files.filter(id => id != file_id)
  }

}

export default Folder


