<template>
  <pre class="code-editor-line" id="codeLineEdit" v-highlightjs="sc" @click="(e) => onClickLine(e, i)">
    <code class="javascript" ref="codeLineEdit">
    </code>
  </pre>
</template>

<script>
export default {
  props: ['sc', 'i', 'onClickLine'],
  mounted () {
    let line = $('code')
  }
}
</script>


