import Folder from '../../models/folder'
import File from '../../models/file'
import filesystemDB from '../../db'
import utils from '../../utils'

const state = {
  currentPath: '/',
  currentFileId: null,
  currentFiles: [],
  files: []
}

// getters - functions for computing derived state based on store state
// ie. getById
const getters = {
  getSubfile: (state) => (id) => {
    let files = state.files.filter(file => file.getId() == id)
    return files.length == 1 ? files[0] : null
  },
  // validate if name already existed, check based on same type and same location
  validName: (state) => (newfile) => {
    let hasName = state.files.filter(file => file.constructor === newfile.constructor && 
      file.getLocation() == newfile.getLocation() && file.getFilename() == newfile.getFilename() )
    return hasName.length == 0
  },
  getCurrentFile: (state, getters) => {
    if (state.currentFiles.length == 0) {
      return null
    }
    let files = state.currentFiles.filter(f => f.getId() == state.currentFileId)
    return files.length == 1 ? files[0] : state.currentFiles[0]
  },
  // content
  getContentLength: (state, getters) => {
    let file = getters.getCurrentFile
    return file ? file.getContent().length : 0
  },
  getContentLine: (state, getters) => (indx) => {
    let file = getters.getCurrentFile
    return file ? file.getContent()[indx] : null
  },
  getContentLineLength: (state, getters) => (indx) => {
    let file = getters.getCurrentFile
    return file ? file.getContent()[indx].length : 0
  },
  getContent: (state, getters) => {
    let file = getters.getCurrentFile
    return file ? file.getContent() : []
  },
  sortSubfileIds: (state, getters) => (ids) => {
    let subfiles = ids.map(id => getters.getSubfile(state)(id)).filter(id => id != null)
    subfiles.sort(utils.sortByName)
    return subfiles.map(f => f.getId())
  }
}

// actions - functions that commit mutations; can contain arbitrary asynchronous operations
const actions = {
  setFiles ({state, commit}, files) {
    commit('setFiles',files)
  },
  loadFiles ({state, commit}) {
    commit('loadFiles')
  },
  // create new file/folder
  createFile ({state, commit}, file) {
    commit('createFile', file)
  },
  removeFile ({state, commit}, file_id) {
    commit('removeFile', file_id)
  },
  // open file
  readFile ({state, commit}, file_id) {
    commit('readFile', file_id)
  },
  renameFile ({state, commit}, {file_id, name}) {
    commit('renameFile', {file_id, name})
  },
  // read file that is open
  setCurrentFileId ({state, commit}, file_id) {
    commit('setCurrentFileId', file_id)
  },
  // close file from tabs
  closeFile ({state, commit}, file_id) {
    commit('closeFile', file_id)
  },
  // File contents
  insertLine ({state, commit}, {indx, str}) {
    commit('insertLine', {indx, str})
  },
  editLine ({state, commit}, {indx, str}) {
    commit('editLine', {indx, str})
  },
  removeLine ({state, commit}, indx) {
    commit('removeLine', indx)
  }
}

// mutations - functions that change state in store; can't call them directly
const mutations = {
  loadFiles (state) {
    filesystemDB.loadDB()
  },
  setFiles (state, files) {
    files.sort(utils.sortByName)
    state.files = files
  },
  createFile (state, file) {
    // when creating file use the location to add subfiles/folders to folders
    // check location (case 1: /, case 2: relative)
    if (file) {
      state.files.push(file)
      state.files.sort(utils.sortByName)
      let location = file.getLocation()
      // find folder that this file belongs, add the files in them
      let folders = state.files.filter(f => f instanceof Folder && file.getLocation() == f.getFullLocation())
      if (folders.length == 1) {
        let folder = folders[0]
        // update folder
        folder.addFile(file.getId())
        folder.setFiles(getters.sortSubfileIds(state, getters)(folder.getFiles()))
        filesystemDB.updateFile(folders[0])
      } else {
        let newFolder = new Folder(file.getLocation())
        if (newFolder.getFilename() !== '') {
          newFolder.addFile(file.getId())
          // recursive
          mutations.createFile(state, newFolder)
        }
      }
      filesystemDB.addFile(file)
    }
  },
  removeFile (state, file_id) {
    let files = state.files.filter(f => f._id == file_id)
    // remove file id from folder
    let folders = state.files.filter(f => f instanceof Folder && files[0].getLocation() == f.getFullLocation())
    if (folders.length > 0) {
      folders[0].removeFile(file_id)
        folders[0].setFiles(getters.sortSubfileIds(state, getters)(folders[0].getFiles()))
      filesystemDB.updateFile(folders[0])
    }
    if (files.length > 0) {
      // for files
      if (files[0] instanceof File) {
        // remove file
        state.files = state.files.filter(f => f.getId() != file_id).sort(utils.sortByName)
        state.currentFiles = state.currentFiles.filter(f => f.getId() != file_id)
        filesystemDB.removeFile(file_id)

      // for folders remove subfiles too
      } else if (files[0] instanceof Folder) {
        let subfiles = files[0].getFiles()
        state.files = state.files.filter(f => subfiles.indexOf(f.getId()) == -1 && f.getId() != file_id).sort(utils.sortByName)
        state.currentFiles = state.currentFiles.filter(f => subfiles.indexOf(f.getId()) == -1 && f.getId() != file_id)
        subfiles.forEach(id => filesystemDB.removeFile(id))
        filesystemDB.removeFile(file_id)
      }
    }
  },
  setCurrentFileId (state, file_id) {
    let files = state.currentFiles.filter(f => f.getId() == file_id)
    if (files.length > 0) {
      state.currentFileId = file_id
      state.currentPath = files[0].getLocation()
    }
  },
  readFile (state, file_id) {
    // already opened
    let alreadyOpened = state.currentFiles.filter(f => f.getId() == file_id)
    if (alreadyOpened.length == 0) {
      // find file
      let files = state.files.filter(f => f._id == file_id)
      let currentFileIndx = 0
      // find current file index
      if (state.currentFileId) {
        let currentFilesIndx = state.currentFiles.map(f => f.getId())
        currentFileIndx = currentFilesIndx.indexOf(state.currentFileId)
      }
      if (files.length == 1) {
        state.currentFiles.splice(currentFileIndx, 0, files[0])
        state.currentFileId = files[0].getId()
        state.currentPath = files[0].getLocation()
      }
    } else {
      state.currentFileId = file_id
      state.currentPath = alreadyOpened[0].getLocation()
    }
  },
  renameFile (state, {file_id, name}) {
    let files = state.files.map(f => {
      if (f._id == file_id) {
        f.setName(name)
        filesystemDB.updateFile(f)
      }
      return f
    })
    state.files = files
  },
  closeFile (state, file_id) {
    state.currentFiles = state.currentFiles.filter(f => f._id != file_id)
  },
  insertLine (state, {indx, str}) {
    let file = getters.getCurrentFile(state, getters)
    if (file) {
      let content = file.getContent()
      content.splice(indx, 0, str)
      file.saveContent(content)
      filesystemDB.updateFile(file)
    }
  },
  editLine (state, {indx, str}) {
    let file = getters.getCurrentFile(state, getters)
    if (file) {
      let content = file.getContent()
      content.splice(indx, 1, str)
      file.saveContent(content)
      filesystemDB.updateFile(file)
    }
  },
  removeLine (state, indx) {
    let file = getters.getCurrentFile(state, getters)
    if (file) {
      let content = file.getContent()
      content.splice(indx, 1)
      file.saveContent(content)
      filesystemDB.updateFile(file)
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}


