<template>
  <div class="top-bar">
    <div class="top-max-btn" @click="onClickFullscreen">
      <Fullscreen
        v-if="!fullscreen"
        class="icon" viewBox="0 0 25 25" height="50" width="50"/>
      <FullscreenExit
        v-if="fullscreen"
        class="icon" viewBox="0 0 25 25" height="50" width="50"/>
    </div>
    <div class="top-bar-title">{{title}}</div>
    
  </div>
</template>

<script>
import Fullscreen from '../assets/svg/fullscreen.svg'
import FullscreenExit from '../assets/svg/fullscreen-exit.svg'

export default {
  props: ['onClickFullscreen', 'fullscreen'],
  components: {
    'Fullscreen': Fullscreen,
    'FullscreenExit': FullscreenExit
  },
  computed: {
    file: function () {
      return this.$store.getters.getCurrentFile
    },
    title: function () {
      return this.file ? `${this.file.getFilename()} - ${this.file.getLocation()}` : `Visual Lime`
    }
  }
}
</script>

<style>
.top-bar {
    text-align: center;
    width: 100%;
    background: rgba(17,17,17,0.15);
    font-size: 14px;
    padding: 2px;
}
.top-max-btn {    
  position: absolute;
    left: 0;
    top: 0;
    padding: 5px;
    padding-top: 4px;
    cursor: pointer;
}
.top-max-btn:hover {
  opacity: 0.6;
}
</style>



