<template>
  <div class="modal-container">
    <div class="modal" v-on-click-outside="toggleModal">
      <div class="modal-title">
        <span class="modal-title-text">Create {{mode}}</span>
        <span class="modal-dismiss" @click="toggleModal">&times;</span>
      </div>
      <div class="modal-content">
        <div class="modal-input-container">
          <input 
            ref="modalInput" 
            class="modal-input" 
            type="text" 
            :placeholder="`/path/to/${mode}`" 
            v-model="input" 
            @keyup.enter="onCreate"
            @keyup="onTyping">
          <div class="modal-submit" v-show="showSubmitBtn" @click="onCreate">+</div>
        </div>
        <div class="modal-input-error" v-show="error">
          {{error}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import File from '../models/file'
import Folder from '../models/folder'

export default {
  props: ['toggleModal', 'mode'],
  data () {
    return {
      input: '',
      showSubmitBtn: false,
      error: undefined,
      disable: false
    }
  },
  created () {
    this.input = this.$store.state.filesystem.currentPath
  },
  mounted () {
    this.$refs.modalInput.focus()
  },
  methods: {
    onTyping() {
      this.error = undefined
      let file = this.mode == 'file' ? new File(this.input) : new Folder(this.input)
      // check if the file/folder names already existed 
      let validName = this.$store.getters.validName(file)
      if (!validName) {
        this.error = `The ${this.mode} name already exist.`
        this.disable = true
      }
      if (this.input != '' && validName) {
        this.disable = false
        this.showSubmitBtn = true
      } else {
        this.showSubmitBtn = false
      }
    },
    onCreate() {
      if (!this.disable) {
        switch (this.mode) {
          case 'file':
            let file = new File(this.input)
            this.$store.dispatch('createFile', file)
            break
          case 'folder':
            let folder = new Folder(this.input)
            this.$store.dispatch('createFile', folder)
            break
          default:
            break
        }
        this.toggleModal()
      }
    }
  },
  watch: {
    '$store.state.filesystem.currentPath' (path) {
      this.input = path
    }
  }
}
</script>

<style>
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.modal {
  background: #eee;
  margin: 0 auto;
  height: 85px;
  width: 300px;
}
.modal-title {
  padding: 5px 15px;
  background: #ddd;
  display: flex;
  justify-content: space-between;
}
.modal-dismiss {
  font-weight: bold;
  user-select: none;
  cursor: pointer;
  color: #999;
}
.modal-dismiss:hover {
  opacity: 0.6;
}
.modal-content {
  padding: 5px 15px;
}
.modal-input {
  background: transparent;
  border: 0;
  font-size: 13px;
  outline: 0;
  width: 100%;
}
.modal-input-container {
  border-bottom: 2px solid #999;
  padding: 2px 0;
  display: flex;
}
.modal-input::placeholder {

}
.modal-submit {
  border-radius: 3px;
  background: #ccc;
  color: #fff;
  line-height: 1.3;
  padding: 0 3px;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  user-select: none;
}
.modal-submit:hover {
  opacity: 0.6;
}
.modal-input-error {
  font-size: 12px;
  color: #f00;
  font-weight: bold;
  margin: 2px 0;
}
</style>



