import store from '../store'
import Folder from '../models/folder'
import File from '../models/file'

//prefixes of implementation that we want to test
let indexedDB = window.mozIndexedDB || window.webkitIndexedDB || window.msIndexedDB || window.indexedDB;

//prefixes of window.IDB objects
window.IDBTransaction = window.IDBTransaction || window.webkitIDBTransaction || window.msIDBTransaction;
window.IDBKeyRange = window.IDBKeyRange || window.webkitIDBKeyRange || window.msIDBKeyRange
 
if (!indexedDB) {
    window.alert("Your browser doesn't support a stable version of IndexedDB.")
}

var db, objStore;


const filesystemDB = {
  loadDB () {
    let request = indexedDB.open("database");
    // let files
      request.onerror = function(event) {
        console.log(event.target.error)
      };
      
      request.onupgradeneeded = function(event) {
        db = event.target.result
        let transaction = event.target.transaction
        let objectStore = db.createObjectStore("files", {keyPath: "_id"})
        objectStore.transaction.oncomplete = function (event) {
          objStore = db.transaction(["files"]).objectStore("files")
          filesystemDB.getData().then(function(files) {
            store.dispatch('setFiles', files)
          })
        }
      } 

      request.onsuccess = function(event) {
        db = event.target.result
        objStore = db.transaction(["files"]).objectStore("files")

        objStore.transaction.oncomplete = function (event) {
          filesystemDB.getData().then(function(files) {
            store.dispatch('setFiles', files)
          })
        }
      };
  },
  getData () {
    let files = []
    let objStore = db.transaction(["files"]).objectStore("files")
    return new Promise(function (resolve, reject) {
      let req = objStore.openCursor()
      req.onsuccess = function (event) {
        let cursor = event.target.result
        if (cursor) {
          let file
          if (cursor.value._extension) {
            file = new File(cursor.value)
          } else {
            file = new Folder(cursor.value)
          }
          files.push(file)
          cursor.continue()
        } else {
          resolve(files)
        }
      }
      req.onerror = function (event) {
        reject('error getting data')
      }
    })
  },
  addFile (file) {
    let objStore = db.transaction(["files"], "readwrite").objectStore("files")
    let req = objStore.add(file)
    req.onsuccess = function (event) {
      console.log("success")
    }
    req.onerror = function (event) {
      console.log("error")
    }
  },
  updateFile (file) {
    let objStore = db.transaction(["files"], "readwrite").objectStore("files")
    let req = objStore.get(file.getId())
    req.onsuccess = function (event) {
      let data = event.target.result
      data = file
      let update = objStore.put(data)
      update.onerror = function (event) {
        console.log("error")
      }
      update.onsuccess = function (event) {
        console.log("success")
      }
    }
    req.onerror = function (event) {
      console.log("error")
    }
  },
  removeFile (file_id) {
    let objStore = db.transaction(["files"], "readwrite").objectStore("files")
    let req = objStore.delete(file_id)
    req.onsuccess = function (event) {
      console.log("success")
    }
    req.onerror = function (event) {
      console.log("error")
    }
  }
}

export default filesystemDB


