<template>
    <div class="code-editor-container" ref="codeEditorContainer">
      <div class="code-editor-line">
        <div class="code-line" v-for="line in contentLength">{{line}}</div>
      </div>
      <div class="code-editor" refs="codeEditor" @click="onClickEditor" v-if="file">
        <pre 
          class="code-editor-line" 
          id="codeLineEdit" 
          v-highlightjs="sc" 
          v-for="(sc, i) in source" 
          @click="(e) => onClickLine(e, i)" 
          @mouseup="(e) => onMouseUp(e, i)" 
          @mousedown="(e) => onMouseDown(e, i)">
            <code class="javascript" ref="codeLineEdit" >
            </code>
        </pre>
        <div class="cursor" ref="cursor" :style="{top: cursorTop+'px', left: cursorLeft+'px'}"></div>
        <span v-if="debug">disabled: {{disableInput ? 'true' : 'false'}}; {{currentLine}}, {{currentLineChar}}; hl: {{selectionStart}} {{selectionEnd}}</span>
          <pre ref="currentLine" id="codeLine" class="code-editor-line code-editor-string" v-highlightjs="currentString">
            <code class="javascript">
            </code>
          </pre>
      </div>
      
      <Placeholder v-if="!file"/>

    </div>
</template>

<script>
import 'highlightjs/styles/atom-one-light.css'
import CodeEditorLine from './CodeEditorLine'
import Placeholder from './Placeholder'
export default {
  props: ['disable'],
  components: {
    'CodeEditorLine': CodeEditorLine,
    'Placeholder': Placeholder
  },
  data () {
    return {
      // sourcecodes: [`const x = "happy";`, `function () {`],
      // source: [
      //   {str: `const x = "happy";`, hlStart: null, hlEnd: null, hl: false},
      //   {str: `function () {`, hlStart: null, hlEnd: null, hl: false}
      // ],
      cursorTop: 0,
      cursorLeft: 0,
      currentLine: 0,
      currentLineChar: 0,
      latency: 1,
      shiftPressed: false,
      ctrlPressed: false,
      cmdPressed: false,
      // disable input when repositioning
      disableInput: false,
      showCursor: false,
      keyCode: {
        left: 37,
        right: 39,
        down: 40,
        up: 38,
        backspace: 8,
        deletecode: 46,
        forwardslash: 191,
        openbracket: 219,
        closebracket: 221,
        space: 32,
        enter: 13,
        tab: 9,
        shift: 16,
        ctrl: 17
      },
      alphaCode: [65, 90],
      numCode: [48, 57],
      numpadCode: [96, 105],
      symbolCode1: [106, 107, 109, 110, 111], // not range
      symbolCode2: [186, 192],
      symbolCode3: [219, 222],
      cmd: [91, 93, 224],
      selectionStart: {
        line: 0,
        char: 0
      },
      selectionEnd: {
        line: 0,
        char: 0
      },
      html: false,
      start: 0,
      end: 0,
      highlighting: false,
      highlightStartLine: null,
      highlightEndLine: null,
      selection: [],
      currentHighlightLine: null,
      debug: false,
      lineHeight: 33
    }
  },
  mounted () {
    // catch typing globally
    let that = this
    // document.onselectionchange = function (e) {
    //   console.log('changed selection', e)
    // }
    // window.onmousedown = function (e) {
    //   console.log('clicking down', e, e.offsetX, e.offsetY)
    //   that.selectionStart = {
    //     line: Math.floor(e.offsetY/25.19),
    //     char: Math.floor(e.offsetX/8)
    //   }
    // }
    // window.onmouseup = function (e) {
    //   console.log('releasing mouse', e, e.offsetX, e.offsetY, Math.floor(e.offsetX/8), Math.floor(e.offsetY/25.19))
    //   that.selectionEnd = {
    //     line: Math.floor(e.offsetY/25.19),
    //     char: Math.floor(e.offsetX/8)
    //   }

    //   let range = document.createRange()
    //   console.log($('#codeLineEdit'))
    //   let line = that.$refs.codeLineEdit[Math.floor(e.offsetY/25.19)]
    //   console.log(Math.floor(e.offsetY/25.19), line.textContent)

    //   range.selectNodeContents(line)
    //   console.log('start', Math.floor(e.offsetX/8))
    //   // range.setStart(line, that.selectionStart.char)
    //   // range.setEnd(line, Math.floor(e.offsetX/8))
    //   // that.currentLineChar = Math.floor(e.offsetY/25.19)
    //   // that.currentLine = Math.floor(e.offsetY/25.19)
    //   // that.repositionCursor()
    // }
    window.onkeyup = function (e) {
      if (!that.disable) {
        let kc = e.keyCode
        if ([17, 91, 93, 224].indexOf(kc) > -1) {
          // when released
          that.cmdPressed = false
          that.ctrlPressed = false
        }
        if (kc == that.keyCode.shift) {
          that.shiftPressed = false
        }
      }
    }
    window.onkeydown = function (e) {
      if (!that.disable) {
        that.repositionPage()
        console.log(e.keyCode)
        // global variables
        let kc = e.keyCode
        let {left, right, down, up, enter, backspace, 
          deletecode, shift, tab, ctrl, openbracket, closebracket, forwardslash} = that.keyCode
        let currentLineLength = that.$store.getters.getContentLineLength
        // arrows
        switch (kc) {
          case ctrl:
            that.ctrlPressed = true
            break
          case 91:
            that.cmdPressed = true
            break
          case 93:
            that.cmdPressed = true
            break
          case 224:
            that.cmdPressed = true
            break
          case openbracket:
            e.preventDefault()
            e.stopPropagation()
            if (!that.cmdPressed && !that.ctrlPressed && that.isText(kc)) {
              that.insertText(e.key)
            } else if (that.cmdPressed || that.ctrlPressed) {
              that.unindentLine()
            }
            break
          case closebracket:
            e.preventDefault()
            e.stopPropagation()
            if (!that.cmdPressed && !that.ctrlPressed && that.isText(kc)) {
              that.insertText(e.key)
            } else if (that.cmdPressed || that.ctrlPressed) {
              that.indentLine()
            }
            break
          case forwardslash:
            if (that.cmdPressed || that.ctrlPressed) {
              that.commentLine()
            } else {
              that.insertText(e.key)
            }
            break
          case tab:
            e.preventDefault()
            that.tabLine()
            break
          case shift:
            that.shiftPressed = true
            break
          case backspace:
            e.preventDefault()
            e.stopPropagation()
            that.removeText()
            break
          case deletecode:
            e.preventDefault()
            e.stopPropagation()
            that.removeText()
            break
          case left:
            that.navigateLeft()
            break
          case right: 
            that.navigateRight()
            break
          case up:
            that.navigateUp()
            break
          case down:
            that.navigateDown()
            break
          case enter:
            that.newLine()
            break
          default:
            if (that.isText(kc)) {
              that.insertText(e.key)
            }
            break
        }
        that.repositionCursor()
      }
    }
  },
  methods: {
    isText (code) {
      let isAlpha = this.inRange(code, this.alphaCode)
      let isNum = this.inRange(code, this.numCode) || this.inRange(code, this.numpadCode)
      let isSymbol = this.symbolCode1.indexOf(code) > -1 || this.inRange(code, this.symbolCode2) || this.inRange(code, this.symbolCode3)
      let isSpace = code == this.keyCode.space
      return isAlpha || isNum || isSymbol || isSpace
    },
    inRange (code, range) {
      let start = range[0]
      let end = range[1]
      return code >= start && code <= end
    },
    insertText (char) {
      let line = this.getCurrentLine()
      if (line) {
        this.setCurrentLine(line.slice(0, this.currentLineChar) + char + line.slice(this.currentLineChar))
      } else {
        this.setCurrentLine(char)
      }

      this.currentLineChar += 1
    
    },
    removeText () {
      let line = this.getCurrentLine()
      if (this.currentLineChar > 0) {
        this.setCurrentLine(line.slice(0, this.currentLineChar-1) + line.slice(this.currentLineChar))
        this.currentLineChar -= 1
      } else if (this.currentLineChar == 0) {
        // merge line when char == 0 && there are lines above
        this.mergeLine()
      }
    },
    navigateLeft () {
      let line = this.getCurrentLine()
      if (this.currentLineChar > 0) {
        this.currentLineChar -= 1
      } else {
        this.navigateUp('end')
      }
    },
    navigateRight () {
      let currentLineLength = this.$store.getters.getContentLineLength(this.currentLine)
      if (this.currentLineChar < currentLineLength) {
        this.currentLineChar += 1
      } else {
        // move below line if there are lines below
        this.navigateDown('start')
      }
    },
    navigateUp (position) {
      if (this.currentLine > 0) {
        this.currentLine -= 1
        let currentLineLength = this.$store.getters.getContentLineLength(this.currentLine)
        this.currentLineChar = position != 'end' ? Math.min(this.currentLineChar, currentLineLength) : currentLineLength
        this.scrollUp()
      }
    },
    navigateDown (position) {
      let linesCount = this.getLinesCount()
      if (this.currentLine < linesCount-1) {
        this.currentLine += 1
        let currentLineLength = this.$store.getters.getContentLineLength(this.currentLine)
        this.currentLineChar = position != 'start' ? Math.min(this.currentLineChar, currentLineLength) : 0
        this.scrollDown()
      }
    },
    newLine () {
      let line = this.getCurrentLine()
      if (line) {
        // split line at char and 
        let str_start = line.slice(0, this.currentLineChar)
        let str_end = line.slice(this.currentLineChar)
        this.insertLine(this.currentLine+1, str_end)
        this.setCurrentLine(str_start)
        this.currentLine += 1
        this.currentLineChar = 0
      } else {
        this.insertLine(this.currentLine+1, '')
        this.currentLine += 1
        this.currentLineChar = 0
      }
      this.scrollDown()
    },
    mergeLine () {
      if (this.currentLine > 0) {
        this.currentLine -= 1
        this.currentLineChar = this.$store.getters.getContentLineLength(this.currentLine)
        let newLine = this.getCurrentLine() + this.getLine(this.currentLine+1)
        this.setCurrentLine(newLine)
        this.removeLine(this.currentLine+1)
      }
      this.repositionPage()
    },
    commentLine () {
      if (this.cmdPressed || this.ctrlPressed) {
        let line = this.getCurrentLine()
        if (line) {
          let newLine = line.includes('//') ? line.replace(/^\/\/\s/, '') : '// ' + line
          this.setCurrentLine(newLine)
          this.currentLineChar = line.includes('//') ? this.currentLineChar-3 : this.currentLineChar+3
        } else {
          this.setCurrentLine('// ')
          this.currentLineChar += 3
        }
      } 
    },
    tabLine () {
      let line = this.getCurrentLine()
      let str_start = line.slice(0, this.currentLineChar)
      let str_end = line.slice(this.currentLineChar)
      let newLine = str_start + '  ' + str_end
      this.setCurrentLine(newLine)
      this.currentLineChar += 2
    },
    indentLine () {
      if (this.cmdPressed || this.ctrlPressed) {
        let line = this.getCurrentLine()
        this.setCurrentLine('  ' + line)
        this.currentLineChar += 2
      }
    },
    unindentLine () {
      let line = this.getCurrentLine()
      if ((this.cmdPressed || this.ctrlPressed) && /^\s{2}/.test(line)) {
        this.setCurrentLine(line.replace(/^\s{2}/, ''))
        this.currentLineChar -= 2
      }
    },
    onClickEditor () {

    },
    onClickLine (e, i) {
      // this.currentLine = i
      // this.currentLineChar = Math.min(Math.floor(e.offsetX/8), this.getCurrentLine().length)
      // this.repositionCursor()
    },
    // mouse release
    onMouseUp (e, i) {
      // this.highlighting = false
      // for (var indx = 0; indx < i-1; indx++) {
      //   this.source[indx].hlEnd = this.source[indx].length
      // }
      // this.source[i].hlEnd = Math.floor(e.offsetX/8)
      // this.currentLine = i
      // this.currentLineChar = Math.min(Math.floor(e.offsetX/8), this.getCurrentLine().length)
      // this.repositionCursor()
    },
    onMouseDown (e, i) {
      // this.highlighting = true
      // this.currentHighlightLine = i
      // console.log('x, y: ', e.offsetX, e.offsetY)
      // // reset all highlights
      // // for (var indx = 0; indx < this.source.length; indx++) {
      // //   this.source[indx].hlEnd = 0
      // //   this.source[indx].hlStart = 0
      // //   this.source[indx].hl = false
      // // }
      // this.source[i].hl = true
      // if (!this.source[i].hlStart) {
      //   this.source[i].hlStart = Math.floor(e.offsetX/8)
      // } else {
      //   // this.source[i].hlEnd = Math.floor(e.offsetX/8)
      // }
      this.currentLine = i
      let currentLineLength = this.$store.getters.getContentLineLength(this.currentLine)
      this.currentLineChar = Math.min(Math.floor(e.offsetX/8), currentLineLength)
      this.repositionCursor()
    },
    onEnterHoverLine (e, i) {
      if (this.highlight) {
        console.log('highlighting & hovering')
        // highlight all previous lines
        for (var indx = 0; indx < this.source.length; indx++) {
          // this.source[indx].hlEnd = null
          // this.source[indx].hl = false
        }
      }
    },
    onHoverLine (e, i) {
      if (this.highlighting) {
        this.source[i].hl = true
        this.source[i].hlEnd = Math.floor(e.offsetX/8)
        console.log('hovering', Math.floor(e.offsetX/8))
        this.currentLine = i
        this.currentLineChar = Math.min(Math.floor(e.offsetX/8), this.getCurrentLine().length)
        this.repositionCursor()
      }
    },
    onLeaveHoverLine (e, i) {
      if (this.highlighting) {
        this.source[i].hlEnd = this.getCurrentLine().length
      }
    },
    // Main fct for content mutations
    getCurrentLine () {
      // return this.source[this.currentLine].str
      return this.$store.getters.getContentLine(this.currentLine)
    },
    getLine (indx) {
      // return this.source[indx].str
      return this.$store.getters.getContentLine(indx)
    },
    getLinesCount () {
      // return this.source.length
      return this.$store.getters.getContentLength
    },
    setCurrentLine (str) {
      // this.source[this.currentLine].str = str
      this.$store.dispatch('editLine', {indx: this.currentLine, str})
    },
    // replace line at i with str
    insertLine (indx, str) {
      // this.source.splice(i, 0, {str, hlStart: 0, hlEnd: 0, hl: false})
      this.$store.dispatch('insertLine', {indx, str})
    },
    removeLine (indx) {
      // this.source.splice(indx, 1)
      this.$store.dispatch('removeLine', indx)
    },
    repositionCursor () {
      let that = this
      setTimeout(function () {
        that.cursorLeft = that.$refs.currentLine.offsetWidth-2.6
        // that.cursorLeft = that.$refs.currentLine.offsetWidth-6.5
        that.cursorTop = (that.$refs.currentLine.offsetHeight+0.19)*(that.currentLine)
        that.disableInput = false
      }, that.latency)
    },
    repositionPage () {
      let that = this
      setTimeout(function () {
        let cursorPosition = that.$refs.cursor.getBoundingClientRect().y
        let viewHeight = that.$refs.codeEditorContainer.offsetHeight
        if (cursorPosition < 54 || cursorPosition > viewHeight) {
          that.$refs.codeEditorContainer.scrollTop = (that.$refs.currentLine.offsetHeight+0.19)*(that.currentLine)
        }
      }, that.latency+1)
      
    },
    scrollUp () {
      let that = this
      setTimeout(function () {
        let cursorPosition = that.$refs.cursor.getBoundingClientRect().y
        let viewHeight = that.$refs.codeEditorContainer.offsetHeight
        // scrollup if cursor is at top
        if (cursorPosition > 53 && cursorPosition < 58) {
          that.$refs.codeEditorContainer.scrollTop -= 25.19
        }
      }, that.latency+1)
    },
    scrollDown () {
      let that = this
      setTimeout(function () {
        let cursorPosition = that.$refs.cursor.getBoundingClientRect().y
        let viewHeight = that.$refs.codeEditorContainer.offsetHeight+25.19
        // scrolldown if cursor is at bottom
        if (cursorPosition > viewHeight) {
          that.$refs.codeEditorContainer.scrollTop += 25.19
        }
      }, that.latency+1)
    }
  },
  computed: {
    currentString: function () {
      let str = this.getCurrentLine()
      return str ? str.slice(0, this.currentLineChar) : ''
    },
    width: function () {
      // return document.getElementById('codeLine')
      return this.$refs.currentLine ? this.$refs.currentLine.offsetWidth-2.6 : 0
    },
    contentLength: function () {
      return this.$store.getters.getContentLength
    },
    file: function () {
      let file = this.$store.getters.getCurrentFile
      return file
    },
    source: function () {
      return this.$store.getters.getContent
    }
  },
  watch: {
    file (v) {
      this.currentLine = 0
      this.currentLineChar = 0
      this.repositionCursor()
      return v
    }
  }
}
</script>
<style>
.code-line {
  width: 20px;
  text-align: right;
  padding-right: 5px;    
  font-size: 13px;
  line-height: 1.9;
  /*line-height: 2.5;*/
  height: 25.19px;
  user-select: none;
}
#currentLineInput {
      position: fixed;
    right: 0;
    top: 0;
    /*opacity: 0;*/
}
.code-editor-string {
  display: inline-block;
      position: fixed;
    right: 0;
    top: 20px;
    opacity: 0;
}
.code-editor-line {
  margin: 0;
  white-space: normal;
  height: 25.19px;
}
.code-editor-line code {
  min-height: 20px;
  white-space: pre;
  height: 25.19px;
  padding: 2.6px;
}
.code-editor-container {
  height: 95%;
  display: flex;
  overflow-y: auto;
  width: 100%;
}
.code-editor-content {
  margin: 0;    
  line-height: 1.7;
}
.code-editor-content code {
  position: absolute;
    left: 0;
    top: 0;
        width: 100%;
    height: 100%;
}
::selection {
  /*background: transparent;*/
}

.code-editor {
  position: relative;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  cursor: text;
}
.cursor {
  position: absolute;
  top: 0;
  left: 0;    
  /*margin: 6.5px 0;*/
  margin: 1.65px 0;
  height: 20px;
  width: 2px;
  background: #111111;
  animation: blink 1s infinite;
}
.hljs {
  padding: 0.2em;
}
@keyframes blink {
    0%, 49.9%, 100%   { opacity: 0; }
    50%, 99.9% { opacity: 1; }
}
.highlight-text {
  background: #f00;
}
</style>



