<template>
  <div class="editor-content" :class="{'resize': resize}">
    <div class="editor-left">
      <Folders :toggleModal="toggleModal"/>
    </div>
    <div class="editor-middle">
      <TabsBar />
      <CodeEditor :disable="disable"/>
    </div>
  </div>
</template>

<script>
import CodeEditor from './CodeEditor_div'
import TabsBar from './TabsBar'
import Folders from './Folders'
export default {
  props: ['disable', 'toggleModal'],
  components: {
    'CodeEditor': CodeEditor,
    'TabsBar': TabsBar,
    'Folders': Folders
  },
  data () {
    return {
      resize: false
    }
  },
  mounted () {
    if (navigator.userAgent.toLowerCase().indexOf('firefox') > -1) {
      this.resize = true
    }
  }
}
</script>

<style>
#input {
      opacity: 0;
    height: 0px;
    width: 0px;
}
.editor-middle {
  height: 100%;
  flex: 1;
}
.editor-content {
  display: flex;
  width: 100%;
  height: 100%;
}
.editor-left {
  width: 150px;
  background: #f3f3f3;
  height: 100%;
}
.resize {
  height: 97%;
}
</style>



