import Vue from 'vue'
import Router from 'vue-router'
import Editor from '@/pages/Editor'
import CodeEditor_div from '@/components/CodeEditor_div'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Editor',
      component: Editor
    },
    {
      path: '/v2',
      name: 'CodeEditor_div',
      component: CodeEditor_div
    }
  ]
})



